@font-face {
  font-family: "Larsseit-family"; 
  src: url("../fonts/Larsseit-family/Larsseit.eot"); 
  src: 
  url("../fonts/Larsseit-family/Larsseit.woff") format("woff"), 
  url("../fonts/Larsseit-family/Larsseit.ttf") format("truetype"); 
  font-style: normal; 
  font-weight: normal; 
}

@font-face {
  font-family: "Larsseit-family"; 
  src: url("../fonts/Larsseit-family/Larsseit-Bold.eot"); 
  src: 
  url("../fonts/Larsseit-family/Larsseit-Bold.woff") format("woff"), 
  url("../fonts/Larsseit-family/Larsseit-Bold.ttf") format("truetype"); 
  font-style: normal; 
  font-weight: bold; 
}
   
